.App {
	text-align: center;
}

.container {
	max-width: 1000px;
	margin: 50px auto;
	padding: 0 30px;
}

input {
	@media (max-width: 860px) {
		width: 100%;
	}
	width: 60%;
	padding: 0 20px;
	height: 50px;
	margin-bottom: 50px;
}

.items {
	@media (max-width: 650px) {
		flex-direction: column;
		a {
			margin-bottom: 30px;
			font-weight: bold;
			// word-wrap: wrap;
		}
		img {
			margin-bottom: 20px;
		}
	}
	display: flex;
	border-top: 1px solid #000;
	border-left: 1px solid #000;
	border-right: 1px solid #000;
	margin: 0 auto;
	padding: 17px;
	cursor: pointer;
	justify-content: space-between;
	align-items: center;
	&:last-child {
		margin-bottom: 50px;
		border-bottom: 1px solid #000;
	}

	&__repo {
		display: flex;
		flex-direction: column;
		span {
			margin-bottom: 10px;
			display: inline-flex;
		}
	}
}

.user-title {
	align-self: center;
	margin: 0 auto;
}

.user {
	margin-bottom: 50px;
	@media (min-width: 768px) {
		display: flex;
		margin-top: 50px;
		justify-content: center;

		&__info {
			margin-left: 50px;
			h3 {
				margin-bottom: 10px;
			}
		}
	}
	@media (max-width: 400px) {
		img {
			width: 200px;
			height: 200px;
		}
	}
}
